.personalCards {
    margin-top: 30px;
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    gap: 70px;
}

.personalCard {
    border-radius: 10px;
    width: 35%;
    border: 1px solid var(--semi-beige-color);
    padding: 20px;
    background-color: var(--base-bg-color);
}

.personName {
    font-weight: 600;
    font-size: 0.9em;
}

.personDecription {
    font-size: 0.8em;
    line-height: 1.6em;
}

.person {
    height: 25vw;
    background-size: cover;
    background-position: center center;
}

.person1 {
    background-image: url(../../../assets/images/persons/fake-photo.png);
}

.person2 {
    background-image: url(../../../assets/images/persons/fake-photo.png);
}

.person3 {
    background-image: url(../../../assets/images/persons/fake-photo.png);
}

.person4 {
    background-image: url(../../../assets/images/persons/fake-photo.png);
}

@media screen and (max-width: 960px) {
    .personalCards {
        gap: 5vw;
    }
}

@media screen and (max-width: 640px) {
    .personalCard {
        width: 80%;
        margin: 0 auto;
    }
    .person {
        height: 80vw;
    }
}